@mixin button {
    display: flex;
    min-height: 44px;
    max-width: 44px;
    min-width: 180px;
    max-width: 180px;
    border-radius: 20px;
    align-items: center;
    justify-content: center;
    font-size: $font14px;
    @include RalewayBold;
    background: $green;
    color: $white;
    cursor: pointer;
    @include transitionDefault;

    &:hover {
        @include transitionDefault;
        background: $black;
        color: $white; } }

@mixin headerButton {
    @include button;
    border: solid 2px $white;
    background: transparent;

    &:hover {
        color: $green;
        border-color: $green;
        background: transparent; } }







