.navigation {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    height: 60px;
    width: 100%;
    background: transparent;
    z-index: 999;
    transition: .4s all;
    transition-delay: .2s;

    &.green {
        background: rgba(45, 221, 172, .8);
        transition: .4s all;
        transition-delay: .2s; }

    &-container {
        @include container;
        position: relative;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        margin-right: 2%;
        margin-left: 2%; }

    &-logo {
        position: relative;
        color: $white;
        font-size: $font28px;
        display: flex;
        @include ProximaNovaRegular;
        cursor: pointer;

        &::after {
            content: '.';
            position: absolute;
            right: -10px;
            bottom: 0;
            color: $green; } }

    &-list {
        position: relative;
        display: none;
        flex-direction: row;
        width: 35%;
        justify-content: space-between;
        margin: 0;
        padding: 0;
        @include transitionDefault;

        &-link {
            color: $white;
            font-size: $font16px;
            @include RalewayBold;
            @include transitionDefault;

            &:hover {
                color: $green;
                @include transitionDefault; } }

        &-icon {
            fill: $white;
            height: 22px;
            width: 22px;
            @include transitionDefault;
            cursor: pointer;

            &:hover {
                fill: $green;
                @include transitionDefault; } }

        &.responsive {
            overflow: visible;
            display: flex;
            position: fixed;
            top: 0;
            left: 0;
            height: 100vh;
            width: 100vw;
            z-index: 999;
            background: $navigationActive;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            @include transitionDefault; }

        &.responsive {
            .navigation-list-link {
                margin-bottom: .75em;
                font-size: $font22px;
                @include transitionDefault; } } }

    &-button {
        position: absolute;
        top: 16px;
        right: 1%;
        height: 30px;
        width: 30px;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;
        @include transitionDefault;

        &-item {
            height: 4px;
            width: 30px;
            border-radius: 2px;
            background: $white;
            @include transitionDefault; }

        &:hover &-item {
            background: $green;
            @include transitionDefault; } }

    @include afterLaptop {
        &-container {
            width: 96%;
            margin: 0 auto; }

        &-list {
            display: flex;

            &.responsive {
                position: static;
                background: none;
                height: auto;
                width: 35%;
                flex-direction: row;
                justify-content: space-between; }

            &.responsive {
                .navigation-list-link {
                    margin-bottom: 0;
                    font-size: $font16px; } } }

        &-button {
            display: none; } } }


