.prising {
    min-height: 900px;
    background: $prisingBg;

    &-continer {
        @include container; }

    &-list {
        width: 100%;
        min-height: 590px;
        margin-top: 110px;
        margin-bottom: 100px;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: center;

        &-item {
            min-height: 400px;
            width: 95%;
            background: $white;
            border: solid 1px $white;
            border-radius: 4px;
            overflow: hidden;
            flex-direction: column;
            margin-bottom: 40px;

            &:not(&:last-child) {
                margin: 0; }

            &-cost {
                box-sizing: border-box;
                width: 100%;
                height: 210px;
                flex-direction: column;
                border-bottom: solid 1px $prisingBorderColor;
                padding: 35px;

                &__cost {
                    color: $green;
                    font-size: $font60px;
                    @include RalewayExtraLight;
                    margin-bottom: 30px; }

                &__text {
                    color: $prisingTextColor;
                    font-size: $font18px;
                    @include ProximaNovaRegular; } }

            &-list {
                box-sizing: border-box;
                margin: 0;
                list-style: none;
                width: 100%;
                height: 200px;
                padding: 35px;
                flex-direction: column;
                justify-content: space-between;

                &-item {
                    @include ProximaNovaRegular;
                    font-size: $font18px;
                    color: $prisingTextColor;
                    @include transitionDefault;
                    cursor: pointer;

                    &:hover {
                        color: $green;
                        @include transitionDefault; } } }

            &-button {
                box-sizing: border-box;
                height: 82px;
                background: $green;
                padding-left: 35px;
                padding-right: 35px;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                cursor: pointer;

                &__text {
                    color: $white;
                    font-size: $font18px;
                    @include RalewayBold;
                    text-transform: uppercase;
                    letter-spacing: 0.2em; }

                &__icon {
                    fill: $white;
                    height: 20px;
                    width: 22px; } } } }

    @include upTabletWidth {
        &-list {
            flex-direction: row;
            justify-content: space-around;

            &-item {
                width: 48%; } } }

    @include extralarge {
        &-list {
            justify-content: space-between;

            &-item {
                min-height: 590px;
                width: 345px;
                margin: 0;

                &-cost {
                    height: 210px;
                    padding-top: 50px;
                    padding-bottom: 60px; }

                &-list {
                    height: 300px;
                    padding-top: 90px;
                    padding-bottom: 75px; } } } } }
