.about {
    min-height: 900px;
    width: 100%;
    background: $white;

    &-container {
        @include container; }

    &-content {
        flex-wrap: wrap;
        justify-content: space-around;
        width: 100%;
        min-height: 420px;
        margin-top: 40px;
        margin-bottom: 40px;

        &-item {
            width: 295px;
            height: 420px;
            flex-direction: column;
            align-items: center;
            text-align: center;
            @include transitionDefault;
            cursor: pointer;

            &-round {
                height: 183px;
                width: 183px;
                background: $aboutRoundBg;
                border-radius: 50%;
                justify-content: center;
                align-items: center;
                @include transitionDefault; }

            &.active &-round {
                background: $green;
                @include transitionDefault; }

            &.active &-icon {
                fill: $white;
                @include transitionDefault; }

            &:hover &-round {
                background: $green;
                @include transitionDefault; }

            &:hover &-icon {
                fill: $white;
                @include transitionDefault; }

            &-icon {
                fill: $aboutIconColor;
                @include transitionDefault; }

            &-title {
                color: $black;
                font-size: $font30px;
                @include RalewayRegular;
                margin-top: 60px;
                margin-bottom: 40px; }

            &-text {
                color: $aboutTextColor;
                font-size: $font18px;
                @include ProximaNovaRegular;
                margin: 0; } } }


    &-button {
        @include button;
        margin: 0 auto;
        margin-bottom: 60px; }

    @include upTabletWidth {
        &-content {
            &-item {
                width: 50%; } } }

    @include afterLaptop {
        &-content {
            &-item {
                width: 40%; } } }

    @include extralarge {
        &-content {
            &-item {
                width: 295px; } } } }
