.header {
    position: relative;
    height: 100vh;
    width: 100%;
    overflow: hidden;

    &-slider {
        position: relative;
        width: 100%;
        height: 100vh;

        &-item {
            position: relative;
            width: 100%;
            height: 100vh;
            z-index: 10;

            &-image {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100vh;
                z-index: 15; }

            &-content {
                height: 100vh;
                width: 100vw;
                z-index: 20;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                text-align: center; }

            // &-content
            //     position: absolute
            //     left: 5%
            //     right: 5%
            //     top: 15%
            //     height: 260px
            //     width: 90%
            //     z-index: 20
            //     flex-direction: column
            //     align-items: center
            //     text-align: center

            &-title {
                color: $white;
                font-size: $font60px;
                margin-bottom: 40px;
                margin-top: 0;
                @include RalewayThin;

                &__span {
                    @include RalewayBold; } }

            &-text {
                color: $white;
                font-size: $font24px;
                margin-bottom: 70px;
                margin-top: 0;
                @include ProximaNovaLight; }

            &-btn {
                @include headerButton; } }

        &-dots {
            display: none; }

        &-nav {
            display: flex;

            &-prev,
            &-next {} } } }
