html {
    scroll-behavior: smooth; }
body {
    font-size: 16px;
    position: relative; }

div,ul,li,img,section,a,object,svg {
    display: flex; }

ul {
    padding: 0;
    list-style: none; }

a {
    text-decoration: none; }

section {
    overflow: hidden; }

.title {
    position: relative;
    text-align: center;
    font-size: $font60px;
    color: $textColor;
    @include RalewayExtraLight;
    margin-top: 100px;
    margin-bottom: 40px;

    &::after {
        content: '';
        position: absolute;
        bottom: -40px;
        left: 50%;
        right: 50%;
        transform: translate(-50%, -50%);
        width: 30px;
        height: 1px;
        background: $green; } }

.pubText {
    text-align: center;
    font-size: $font18px;
    @include ProximaNovaRegular;
    margin-top: 40px;
    color: $textColor; }

.owl {
    &-dots {
        display: none;
        justify-content: space-between;
        flex-direction: row;
        position: absolute;
        left: 50%;
        right: 50%;
        transform: translate(-50%, -50%);
        bottom: 2%;
        height: 12px;
        width: 100px;
        z-index: 30; }

    &-dot {
        display: flex;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        border: solid 2px $white;
        background: transparent;
        @include transitionDefault;
        z-index: 40;
        margin: 0;
        padding: 0;

        &.active {
            @include transitionDefault;
            background: $white; } }

    &-nav {
        display: none; }

    @include afterLaptop {
        &-dots {
            display: flex; } } }
