.try {
    // min-height: 760px
    height: 100vh;
    width: 100%;
    background: url('../img/trybg.jpg');
    background-position: center;
    background-size: cover;
    overflow: hidden;

    &-container {
        @include container;
        flex-direction: column;
        justify-content: center;
        align-items: center; }

    &-title {
        color: $white;
        font-size: $font52px;
        text-align: center;
        @include RalewayThin;

        &__span {
            @include RalewayMedium; } }

    &-button {
        @include button;
        height: 50px;
        width: 200px; }

    @include extralarge {
        height: 80vh; } }
